
.container{
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(0, 89, 167);
}
.App{
  height: 690px;
  width:520px;
  background-color: rgb(182, 221, 255);
  border-radius: 25px;
  box-shadow: 10px 10px 10px black;
  padding: 20px;
  overflow-y: scroll;
}
.App::-webkit-scrollbar{
  display: none;

}
.App h1{
  font-family: "Playwrite MX", cursive;
 font-size: 60px;
 display: inline;
 color:  rgb(3, 24, 41);
}
.App img{
  height: 170px;
  width: 170px;
  display: inline;
}
.to-do-input{
  display: flex;
        align-items: center;
        justify-content: center;
       
}

.to-do-input input{
        height: 48px;
        width: 360px;
        border-radius: 50px;
        border:none;
        text-align: center;
        font-size: 18px;
        background-color: rgb(214, 236, 255);
}


.IoAdd{
height: 40px;
width: 40px;
margin-top: 5px;
cursor: pointer;
}
.btn-to-do{
  height: 40px;
 margin-top: 30px;
 width: 100px;
 border: none;
}

.btn-to-do{

  color: #000000;
 /* border: 1px solid rgb(127, 127, 127); */
 background: none;
 cursor: pointer;
}
.active{
  
  color: aliceblue;
  background-color: rgb(4, 48, 86);
}

.to-do-list-area{
/* border: 2px solid black; */
margin-top: 20px;
overflow: hidden;

}
.to-do-list-area h4{
display: inline;
font-family: "Playwrite MX", cursive;
/* text-align: center; */
}
.cross{
 
height: 25px;
width: 25px;
margin: 0px 15px;

}
.cross:hover{
color: red;
cursor: pointer;
}
.check{
  height: 25px;
width: 25px;
margin: 0px 15px;
}
.check:hover{
  color: green;
  cursor: pointer;
}
hr{
  width: 95%;
background-color: #000000;
  border: 0cm;
  height: 2px;
}


.to-do-list-item-com{
  margin-top: 20px;
}
.to-do-list-item-com .check{
   color: green;
   cursor: default;
}




@media only screen and (max-width: 600px) {
  .App{
    height: 664px;
    width:330px;
    padding: 10px;
  }
  .App h1{
   font-size: 39px;
  }
  .App img{
    height: 120px;
    width: 100px; 
  }
  .to-do-input input{
    height: 48px;
    width: 300px;
}

.IoAdd{
  height: 20px;
  width: 20px;
  }
  .btn-to-do{
    height: 40px;
   margin-top: 20px;
   width: 100px;
 
  }
  
  
  .to-do-list-area{

  margin-top: 10px;

  
  }

  .cross{
   
  height: 20px;
  width: 20px;
  margin: 0px 10px;
  
  }
 
  .check{
    height: 17px;
  width: 17px;
  margin: 0px 10px;
  }
  .to-do-list-item-com{
    margin-top: 10px;
  }
}